export const DefaultOptions = {
    imgType: "png",
    returnType: "base64",
    pages: "all",
    scale: 1.0,
};
export const isBrowser = typeof window !== "undefined" && typeof window.document !== "undefined";
export const GetSrcOptions = (src, options) => {
    const { documentOptions, ...rest } = {
        ...DefaultOptions,
        ...options,
    };
    let documentParams;
    if (src instanceof ArrayBuffer) {
        documentParams = { data: src, ...documentOptions };
    }
    else if (src instanceof File) {
        let newSrc = URL.createObjectURL(src);
        documentParams = { url: newSrc, ...documentOptions };
    }
    else {
        documentParams = { url: src, ...documentOptions };
    }
    let opt = { ...rest };
    return { documentParams, opt };
};
export function base64ToBuffer(str) {
    let base64 = str.split(";base64,").pop();
    return base64ToArrayBuffer(base64);
}
function base64ToArrayBuffer(base64) {
    var binary_string = window.atob(base64);
    var len = binary_string.length;
    var bytes = new Uint8Array(len);
    for (var i = 0; i < len; i++) {
        bytes[i] = binary_string.charCodeAt(i);
    }
    return bytes.buffer;
}
export function rangeToArr(start, end) {
    let arr = [];
    let st = start;
    while (st <= end) {
        arr.push(st);
        st++;
    }
    return arr;
}
